export default "<template>\n  <Sandbox\n    id=\"example\"\n    title=\"Example\"\n  >\n    <Preview>\n      <VButton\n        color=\"primary\"\n        dark\n        @click=\"isModalShown = true\"\n      >\n        Open modal\n      </VButton>\n      <VModal\n        v-model=\"isModalShown\"\n        :bordered=\"bordered\"\n        :elevated=\"elevated\"\n        :rounded=\"rounded\"\n        :rounded-lg=\"roundedLg\"\n        :size=\"size\"\n        :persistent=\"persistent\"\n        :width=\"computedWidth\"\n        :position=\"position\"\n        :transition=\"transition\"\n        :close-on-esc=\"closeOnEsc\"\n      >\n        <template\n          v-if=\"areSlotsShown\"\n          #top\n        >\n          top\n        </template>\n        <template\n          v-if=\"areSlotsShown\"\n          #right\n        >\n          right\n        </template>\n        <template\n          v-if=\"areSlotsShown\"\n          #bottom\n        >\n          bottom\n        </template>\n        <template\n          v-if=\"areSlotsShown\"\n          #left\n        >\n          left\n        </template>\n        <VModalHeader>Header</VModalHeader>\n        <VModalBody>\n          Body\n        </VModalBody>\n        <VModalBody>\n          <Knobs style=\"width: 100%;\">\n            <KnobBoolean\n              v-model=\"areSlotsShown\"\n              label=\"Show slots\"\n            />\n            <KnobListItem\n              v-model=\"position\"\n              :items=\"positions\"\n              label=\"Position\"\n            />\n            <KnobListItem\n              v-model=\"size\"\n              :items=\"sizes\"\n              label=\"Size\"\n            />\n            <KnobBoolean\n              v-model=\"persistent\"\n              label=\"Persistent\"\n            />\n            <KnobBoolean\n              v-model=\"closeOnEsc\"\n              label=\"Close on ESC\"\n            />\n            <KnobBoolean\n              v-model=\"bordered\"\n              label=\"Bordered\"\n            />\n            <KnobBoolean\n              v-model=\"elevated\"\n              label=\"Elevated\"\n            />\n            <KnobBoolean\n              v-model=\"rounded\"\n              label=\"Rounded\"\n            />\n            <KnobBoolean\n              v-model=\"roundedLg\"\n              label=\"Rounded large\"\n            />\n            <KnobListItem\n              v-model=\"transition\"\n              :items=\"transitions\"\n              label=\"Transition\"\n            />\n            <KnobNumber\n              v-model=\"width\"\n              :min=\"300\"\n              label=\"Width (min width is 300)\"\n            />\n          </Knobs>\n        </VModalBody>\n        <VModalFooter>Footer</VModalFooter>\n        <VModalFooter>\n          <VButton\n            dark\n            color=\"primary\"\n            @click=\"isModalShown = false\"\n          >\n            Close modal\n          </VButton>\n        </VModalFooter>\n      </VModal>\n    </Preview>\n  </Sandbox>\n</template>\n\n<script>\nimport {\n  Preview,\n  Sandbox,\n  Knobs,\n  KnobListItem,\n  KnobBoolean,\n  KnobNumber\n} from '@/docs/components';\nimport {\n  VButton,\n  VModal,\n  VModalHeader,\n  VModalBody,\n  VModalFooter\n} from '@/components';\nimport { sizes, transitions } from '@/docs/helpers/story-params';\n\nexport default {\n  name: 'ModalExample',\n  components: {\n    Preview,\n    Sandbox,\n    Knobs,\n    KnobListItem,\n    KnobBoolean,\n    KnobNumber,\n    VButton,\n    VModal,\n    VModalHeader,\n    VModalBody,\n    VModalFooter\n  },\n  data () {\n    return {\n      isModalShown: false,\n      areSlotsShown: false,\n      sizes,\n      positions: [\n        'middle',\n        'top',\n        'left',\n        'bottom',\n        'right',\n        'fullscreen'\n      ],\n      transitions,\n      persistent: false,\n      bordered: false,\n      elevated: false,\n      rounded: false,\n      roundedLg: false,\n      size: '-',\n      width: null,\n      position: 'middle',\n      transition: 'none',\n      closeOnEsc: false\n    };\n  },\n  computed: {\n    computedWidth () {\n      return this.width ? Math.max(this.width, 300) : this.width;\n    }\n  }\n};\n</script>\n";